<template>
  <div class="articleDetail container">
    <div class="nav-bread">{{ $t("navBread.des") }}: > <router-link to="/"> {{ $t("navBread.index") }} </router-link> > <router-link to="/about">  {{ $t("navBread.about") }} </router-link></div>
    <div class="nd-content container">
      <div class="clearfix about-content-detail">
        <img :src="detail.image_url" class="pull-left" id="about-img">
        <div class="pull-right detail">
          <h3 id="about-title">{{ detail.title }}</h3>
          <div id="about-content" v-html="detail.content"></div>
        </div>
      </div>
      <div class="about-content-ways">
        <h4>{{ $t("about.contactWays") }}</h4>
        <el-row>
          <el-col :span="8">
            <a class="tel"></a> <span id="about-tel">{{ detail.phone }}</span>
          </el-col>
          <el-col :span="8">
            <a class="email"></a> <span id="about-email">{{ detail.email }}</span>
          </el-col>
          <el-col :span="8">
            <a class="qq"></a> <span id="about-qq">{{ detail.qq }}</span>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
import { getAboutDetail } from '@/api/notice'
export default {
  name: 'articleDetail',
  data() {
    return {
      detail: {}
    }
  },
  mounted() {
    this.initProto()
  },
  methods: {
    initProto() {
      getAboutDetail().then(res => {
        console.log(res)
        if (res.status !== 1000) {
          this.$message.error(res.msg)
          return false
        }
        this.detail = { ...res.data }
      }).catch(() => {
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .articleDetail{
    .nd-content {
      background: white;
      color: #3e4558;
      .about-content{
        background: white;
        font-size: 13px;
      }
      .about-content-detail{
        margin: 0 30px;
        padding: 34px 0 70px;
        border-bottom: 1px dashed #ebecf1;
      }
      .about-content-detail img{
        width: 585px;
        height: auto;
      }
      .about-content-detail .detail{
        width: 510px;
      }
      .about-content-ways{
        width: 1200px;
        padding: 26px 30px 60px;
      }
      .about-content-ways h4{
        padding-bottom: 17px;
        font-weight: normal;
      }
      .about-content-ways {
        /*padding-left: 64px;*/
        font-size: 16px;
        display: table-cell;
        vertical-align: middle;
        height: 40px;
      }

      .about-content-ways span{
        display: inline-block;
        line-height: 40px;
        float: left;
      }
      .about-content-ways a{
        content: ' ';
        display: inline-block;
        float: left;
        width: 40px;
        height: 40px;
        margin-right: 18px;
        border-radius: 20px;
        cursor: pointer;
        transition: all 0.3s ease-in-out;
      }
      .about-content-ways .tel{
        background: #d4d1d9  url(/static/images/number_asset/telIcon.png) center no-repeat;
      }
      .about-content-ways .tel:hover{
        background: #0676b3  url(/static/images/number_asset/telIcon.png) center no-repeat;
      }

      .about-content-ways .email{ 
        background: #d4d1d9  url(/static/images/number_asset/emailIcon.png) center no-repeat; 
      }
      .about-content-ways .email:hover{
        background: #c838ac  url(/static/images/number_asset/emailIcon.png) center no-repeat;
      }

      .about-content-ways .qq{ 
        background: #d4d1d9  url(/static/images/number_asset/QQIcon.png) center no-repeat; 
      }
      .about-content-ways .qq:hover{
        background: #56aced  url(/static/images/number_asset/QQIcon.png) center no-repeat;
      }
    }
  }
</style>


